<template>
  <div class="homepage">
    <div class="hero-content">
      <div class="title">
        <h1 data-i18n=hero-content.page-title ></h1>
      </div>
      <!-- <img class="9role_logo" src="../../static/logo.jpg"> -->

      <div class="head-line">
        <p data-i18n="quotes.diversity"></p>
        <p data-i18n="hero-content.head-line-2"></p>
        <p data-i18n="hero-content.head-line-3"></p>
      </div>
    </div>

    <div class="content">
      <matrix :teamRoles="team_roles"></matrix>
      <div id="role_questions">
        <!-- <ul data-role="listview" data-inset="true">
          <li class="role_question" v-for="(question, index) in shuffled_role_questions" :key="index" data-icon="false">
            <a href="javascript:void(0);" onclick="this.firstChild.click()">
              <input :id="'question_' + index" type="checkbox" v-model=question.checked data-role="none">
              <label :for="'question_' + index" class="role_question_checkbox" onclick="this.parentElement.firstChild.click()" v-on:click="tally(question.role)"></label>

              <div class="role_question_text">{{question.text}}</div>
            </a>
          </li>
        </ul> -->

        <div class="role_question" v-show="index == 0" v-for="(question, index) in shuffled_role_questions.filter(question => question.shown == false)" :key="index">
          <fieldset class="ui-grid-a">
            <div class="ui-block-a"><input type="button" :value=localized.yes v-on:click="swipeleft(question)"></div>
            <div class="ui-block-b"><input type="button" :value=localized.no v-on:click="swiperight(question)" data-theme="b"></div>
          </fieldset>

          <div class="role_question_text">{{question.text}}</div>
          <div id="progress-bar">
            <div id="progress-status" v-bind:style="{width: (question_counter*100/role_questions.length)+'%'}"></div>
            <div id="progress-stat">
              {{question_counter + '/'+ role_questions.length}}
            </div>
          </div>
        </div>

        <div id="test-result" v-show="all_shown">
          <form action="https://formspree.io/jq@angelabor.com" method="POST">
            <!-- <matrix :teamRoles="team_roles"></matrix> -->
            <h3 data-i18n="result.title"></h3>

            <div class="test-result-text">
              <textarea name="Teammate Suggestions" v-model="suggestions" readonly="readonly" data-role="none" rows="12"></textarea>

              <div v-for="team_role in team_roles" v-if="!team_role.backstage" :key="'comment_' + team_role.id">
                <img class="role_icon result" :src="team_role.src" />
                <textarea :name="'Team Role: ' + team_role.name" v-model="team_role.comment" readonly="readonly" data-role="none" rows="5"></textarea>
              </div>

              <div v-for="team_role in team_roles" :key="'result_' + team_role.id">
                <input type="hidden" :name="team_role.name" :value="(!team_role.backstage ? 'Pass' : 'Miss') + ', Score: ' + team_role.tally + '/' + num_questions(team_role.id)">
              </div>
            </div>

            <div class="email-data">
              <input type="hidden" name="_subject" data-i18n="[value]head.title" />
              <input type="text" name="_cc" :placeholder=localized.yourEmail />
              <input class="button" type="submit" data-i18n="[value]general.send" data-theme="b">
            </div>
            <!-- <input type="text" name="_gotcha" style="display:none" /> -->
            <!-- <input type="hidden" name="_next" value="http://antslabor.com" /> -->
          </form>
        </div>
      </div>

    </div>
    <br>
    <p data-i18n="quotes.better"></p>
  </div>
</template>

<script>
import BelbinMatrix from './BelbinMatrix.vue'

export default {
  name: 'TeamRoles',
  components: {
    'matrix': BelbinMatrix
  },
  methods: {
    next: function (question) {
      question.shown = true
      this.question_counter += 1
    },
    swipeleft: function (question) {
      question.checked = true
      this.next(question)
    },
    swiperight: function (question) {
      question.checked = false
      this.next(question)
    },
    num_questions: function (role) {
      return this.role_questions.filter(question => (question.role === role)).length
    },
    tally: function (role) {
      return this.role_questions.filter(question => (question.role === role && question.checked)).length
    },
    show: function (role, tally) {
      var roleInfo = this.team_roles.find(teamRole => teamRole.id === role)
      roleInfo.tally = tally
      roleInfo.backstage = tally < (this.num_questions(role) / 2)
    },
    shuffle: function (array) {
      var currentIndex = array.length
      var temporaryValue
      var randomIndex

      // While there remain elements to shuffle...
      while (currentIndex !== 0) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex)
        currentIndex -= 1

        // And swap it with the current element.
        temporaryValue = array[currentIndex]
        array[currentIndex] = array[randomIndex]
        array[randomIndex] = temporaryValue
      }

      return array
    }
  },
  computed: {
    all_shown: function () {
      return (this.role_questions.filter(question => question.shown === false).length === 0)
    },
    suggestions: function () {
      // eslint-disable-next-line
      var suggestion = i18next.t('result.suggestion.opening')
      var missingRoles = this.team_roles.filter(teamRole => teamRole.backstage === true)
      if (missingRoles.length === 0) {
        // eslint-disable-next-line
        return i18next.t('suggestion.godlike')
      }
      for (var i = 0; i < missingRoles.length; i++) {
        suggestion += '\n\n - ' + missingRoles[i].suggest
      }
      return suggestion
    },
    shuffled_role_questions: function () {
      return this.shuffle(this.role_questions)
    },
    tally_innovator: function () {
      return this.tally('innovator')
    },
    tally_explorer: function () {
      return this.tally('explorer')
    },
    tally_analyst: function () {
      return this.tally('analyst')
    },
    tally_coordinator: function () {
      return this.tally('coordinator')
    },
    tally_implementer: function () {
      return this.tally('implementer')
    },
    tally_expert: function () {
      return this.tally('expert')
    },
    tally_driver: function () {
      return this.tally('driver')
    },
    tally_teammate: function () {
      return this.tally('teammate')
    },
    tally_proofreader: function () {
      return this.tally('closer')
    }
  },
  watch: {
    tally_innovator: function (tally) {
      this.show('innovator', tally)
    },
    tally_explorer: function (tally) {
      this.show('explorer', tally)
    },
    tally_analyst: function (tally) {
      this.show('analyst', tally)
    },
    tally_coordinator: function (tally) {
      this.show('coordinator', tally)
    },
    tally_implementer: function (tally) {
      this.show('implementer', tally)
    },
    tally_expert: function (tally) {
      this.show('expert', tally)
    },
    tally_driver: function (tally) {
      this.show('driver', tally)
    },
    tally_teammate: function (tally) {
      this.show('teammate', tally)
    },
    tally_proofreader: function (tally) {
      this.show('closer', tally)
    }
  },
  data () {
    return {
      localized: {
        // eslint-disable-next-line
        title: i18next.t('hero-content.page-title'),
        // eslint-disable-next-line
        yes: i18next.t('general.yes'),
        // eslint-disable-next-line
        no: i18next.t('general.no'),
        // eslint-disable-next-line
        yourEmail: i18next.t('support.your-email')
      },
      question_counter: 1,
      team_roles: [
        {
          id: 'innovator',
          // eslint-disable-next-line
          name: i18next.t('team-roles.innovator.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Plant.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.innovator.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.innovator.suggest')
        },
        {
          id: 'explorer',
          // eslint-disable-next-line
          name: i18next.t('team-roles.explorer.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Resource-Investigator.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.explorer.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.explorer.suggest')
        },
        {
          id: 'analyst',
          // eslint-disable-next-line
          name: i18next.t('team-roles.analyst.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Monitor-Evaluator.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.analyst.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.analyst.suggest')
        },
        {
          id: 'implementer',
          // eslint-disable-next-line
          name: i18next.t('team-roles.implementer.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Implementer.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.implementer.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.implementer.suggest')
        },
        {
          id: 'coordinator',
          // eslint-disable-next-line
          name: i18next.t('team-roles.coordinator.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Co-ordinator.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.coordinator.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.coordinator.suggest')
        },
        {
          id: 'expert',
          // eslint-disable-next-line
          name: i18next.t('team-roles.expert.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Specialist.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.expert.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.expert.suggest'),
        },

        {
          id: 'driver',
          // eslint-disable-next-line
          name: i18next.t('team-roles.driver.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Shaper.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.driver.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.driver.suggest'),
        },
        {
          id: 'teammate',
          // eslint-disable-next-line
          name: i18next.t('team-roles.teammate.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Teamworker.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.teammate.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.teammate.suggest')
        },
        {
          id: 'closer',
          // eslint-disable-next-line
          name: i18next.t('team-roles.closer.name'),
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Complete-Finisher.jpg'),
          // eslint-disable-next-line
          comment: i18next.t('team-roles.closer.comment'),
          // eslint-disable-next-line
          suggest: i18next.t('team-roles.closer.suggest')
        }
      ],
      role_questions: [
        {shown: false, checked: false, role: 'innovator', text: 'I am creative, imaginative, free-thinking.'},
        {shown: false, checked: false, role: 'innovator', text: 'I am a wellspring of original ideas.'},
        {shown: false, checked: false, role: 'innovator', text: 'I am good at solving difficult problems in unconventional ways.'},
        {shown: false, checked: false, role: 'innovator', text: 'I prefer to work apart from the team.'},
        {shown: false, checked: false, role: 'innovator', text: 'I tend to ignore less important things.'},

        {shown: false, checked: false, role: 'explorer', text: 'I am outgoing, enthusiastic, communicative.'},
        {shown: false, checked: false, role: 'explorer', text: 'I can be a salesperson who is the source of vitality.'},
        {shown: false, checked: false, role: 'explorer', text: 'I use my curiosity and inquisitive nature to discover ideas and bring back to the team.'},
        {shown: false, checked: false, role: 'explorer', text: 'I explore opportunities and develops contacts.'},
        {shown: false, checked: false, role: 'explorer', text: 'I have a tendency to be optimistic.'},
        {shown: false, checked: false, role: 'explorer', text: 'I am easily diverted.'},
        {shown: false, checked: false, role: 'explorer', text: 'Others are often receptive to me and my thoughts.'},

        {shown: false, checked: false, role: 'analyst', text: 'I am sober, strategic, discerning'},
        {shown: false, checked: false, role: 'analyst', text: 'I seek all options and judge accurately.'},
        {shown: false, checked: false, role: 'analyst', text: 'I provide a logical eye, making impartial judgments where required and weighs up the team\'s options in a dispassionate way.'},
        {shown: false, checked: false, role: 'analyst', text: 'I provide critique with the expectation of improving performance.'},
        {shown: false, checked: false, role: 'analyst', text: 'I have a tendency to make decisions slowly.'},

        {shown: false, checked: false, role: 'coordinator', text: 'I am a mature, confident, excellent chairperson.'},
        {shown: false, checked: false, role: 'coordinator', text: 'I am the decision maker and delegator.'},
        {shown: false, checked: false, role: 'coordinator', text: 'I take on the traditional team-leader role.'},
        {shown: false, checked: false, role: 'coordinator', text: 'I can identify talent and clarify goals.'},
        {shown: false, checked: false, role: 'coordinator', text: 'I focus on the team\'s objectives and draw out team members and delegate work appropriately.'},

        {shown: false, checked: false, role: 'implementer', text: 'I am disciplined, reliable, conservative and efficient.'},
        {shown: false, checked: false, role: 'implementer', text: 'I turn ideas into practical actions and organizes work that needs to be done.'},
        {shown: false, checked: false, role: 'implementer', text: 'I have a tendency to plan a workable strategy and carry it out as efficiently as possible.'},
        {shown: false, checked: false, role: 'implementer', text: 'I tend to resist unproven ideas.'},
        {shown: false, checked: false, role: 'implementer', text: 'I am inflexible and slow to respond to new possibilities.'},
        {shown: false, checked: false, role: 'implementer', text: 'I work systematically and efficiently.'},
        {shown: false, checked: false, role: 'implementer', text: 'I am very well organized.'},

        {shown: false, checked: false, role: 'expert', text: 'I am focused, self-starting and dedicated.'},
        {shown: false, checked: false, role: 'expert', text: 'I provide knowledge & skills in rare supply.'},
        {shown: false, checked: false, role: 'expert', text: 'I bring in-depth knowledge of a key area to the team.'},
        {shown: false, checked: false, role: 'expert', text: 'I have a tendency to overload people with information.'},
        {shown: false, checked: false, role: 'expert', text: 'I tend to overlook or ignore the "big picture".'},
        {shown: false, checked: false, role: 'expert', text: 'I contribute on a narrow front and dwell on the technicalities.'},

        {shown: false, checked: false, role: 'driver', text: 'I am challenging, dynamic, and thrive on pressure.'},
        {shown: false, checked: false, role: 'driver', text: 'I am the courage to overcome obstacles.'},
        {shown: false, checked: false, role: 'driver', text: 'I bring the competitive drive to ensure that the team keeps moving and does not lose focus or momentum.'},
        {shown: false, checked: false, role: 'driver', text: 'I am prone to provocation, impatience, and sometimes offend peoples’ feelings.'},
        {shown: false, checked: false, role: 'driver', text: 'I could be seen as abrasive.'},
        {shown: false, checked: false, role: 'driver', text: 'I can be aggressive and bad humored in my attempts to get things done.'},

        {shown: false, checked: false, role: 'teammate', text: 'I am co-operative, mild, perceptive and diplomatic.'},
        {shown: false, checked: false, role: 'teammate', text: 'I help the team to gel.'},
        {shown: false, checked: false, role: 'teammate', text: 'I have the versatility to identify the work required and complete it on behalf of the team.'},
        {shown: false, checked: false, role: 'teammate', text: 'I am indecisive in crunch situations and tends to avoid confrontation.'},
        {shown: false, checked: false, role: 'teammate', text: 'I am hesitant to make unpopular decisions.'},

        {shown: false, checked: false, role: 'closer', text: 'I am painstakingly conscientious, anxious.'},
        {shown: false, checked: false, role: 'closer', text: 'I can capture errors and omissions.'},
        {shown: false, checked: false, role: 'closer', text: 'I always meet deadlines and work to highest standards.'},
        {shown: false, checked: false, role: 'closer', text: 'I am very concerned with deadlines.'},
        {shown: false, checked: false, role: 'closer', text: 'I will push the team to make sure the job is completed on time.'},
        {shown: false, checked: false, role: 'closer', text: 'I am a perfectionist.'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/css/checkbox.css';

h1, h2 {
  font-weight: bold;
  margin: .6em 0 .8em !important;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
p {
  font-family: Helvetica, Arial, Sans-Serif;
}
p.highlight {
  font-weight: bold;
}

button, input.button {
  border: 2px solid blue;
  border-radius: 25px;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  float: right;
}

input[type=text] {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

textarea {
  padding: 10px;
  width: 100%;
  min-height: 5em;
  max-height: 15em;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-family: Helvetica, Arial, Sans-Serif;
  font-size: 16px;
}

#topBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  border: none;
  outline: none;
  background-color: red;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
}

#topBtn:hover {
  background-color: #555;
}

div.head-line {
